import React, { Suspense, useRef, useState } from "react";
import * as THREE from "three";
import { Canvas, MeshProps, useFrame, useThree, } from "@react-three/fiber";
import "./App.css";
import Model from "./components/UnityLogo.js";
import { Loader, OrbitControls, PerspectiveCamera } from "@react-three/drei";
import { FlexboxContainer, GridContainer, GridItem1, GridItem2, SubText } from "./components/GeneralStyles";
// @ts-ignore
import { Wave } from 'react-animated-text';




function ScrollContainer({ scroll, children }: any) {
  const { viewport } = useThree();
  let group: any = null;
  group = useRef();
  const vec = new THREE.Vector3();
  useFrame(() => group.current.position.lerp(vec.set(0, viewport.height * scroll.current * 5 - ((viewport.height)), 0), 0.1))
  return <group ref={group}>{children}</group>
}


function Plane(props: MeshProps) {
  const ref = useRef();
  const [hover, setHover] = useState(false)

  return (
    <mesh ref={ref} receiveShadow rotation={[-90, 0, 0]} position={[0, -3, 0]}
      onPointerOver={(e) => setHover(true)}
      onPointerOut={(e) => setHover(false)}>

      <planeBufferGeometry attach="geometry" args={[50, 50]} />
      <meshStandardMaterial attach="material" color='#a9a9a9' />

    </mesh>
  )
}
function Scene() {
  const viewport = useThree((state) => state.viewport)
  return (
    <>
      <Model position={[0, -viewport.height, 0]} />
      <Model position={[0, -viewport.height * 2, 0]} />
    </>
  )
}



function App() {
  const scrollRef = useRef(null)
  const scroll = useRef(0)
  const doScroll = (e: any) => (scroll.current = e.target.scrollTop / e.target.scrollHeight)
  return (
    <>



      <GridContainer >
        <GridItem1>
          <FlexboxContainer>
            <Wave text="Muhammed El-Sayed" effect="verticalFadeIn" iterations={1} /> 
            <SubText>  Multi-Media App Developer</SubText>
          </FlexboxContainer>

        </GridItem1>

        <GridItem2>
          <Canvas className="grid-item2" onCreated={(state: any) => state.events.connect(scrollRef.current)}>
            <PerspectiveCamera makeDefault fov={60} position={[0, 15, 20]} rotation={[-0.7, 0, 0]} />
            <Suspense fallback={null}>
              <ScrollContainer scroll={scroll}>
                <Model />
              </ScrollContainer>
            </Suspense>
          
          </Canvas>
          <Loader  initialState={(active) => active} />
        </GridItem2>

     
      </GridContainer>
     
      <div ref={scrollRef} onScroll={doScroll} className="scroll">
        <div style={{ height: `100vh`, pointerEvents: 'none' }}></div>
      </div>

    </>
  );
}

export default App;
